import navigationBar from '../../components/navigationBar.vue'
import { toLogin, login, post ,get} from "../../api/homepage.js"

export default {
  onShow() {
    if (login()) {
      this.userInfo = login()
      this.avator = this.userInfo.avatarUrl

    }
    //console.log(this.userInfo)
  },
  created() {},
  mounted() {

  },
  components: {
    navigationBar,

  },

  data() {
    return {
      avator: "../../static/denglu.png",
      videoTitle: '我要定制',
      userInfo: {},
      username: '',
      telnumber: '',
      openid: '',
      context: '',
      liebiaos:{}

    }
  },
  methods: {
    toLogin() {
      if (!this.userInfo.avatarUrl) {
        wx.navigateTo({
          url: "/pages/login/login"
        });
      }
    },
    async submit() {
      var _this = this
          if(!this.userInfo.openId){
               wx.showToast({
              title: "请先登陆",
              icon:"none",
              duration: 1500,
              mask: true,
            })
               return false
          }else if(this.context===""){
           wx.showToast({
          title: "请填写你的意见和反馈",
          icon:"none",
          duration: 1500,
          mask: true,
        })
           return false
      }else if(this.telnumber===""){
           wx.showToast({
          title: "请填写你电话号码",
          icon:"none",
          duration: 1500,
          mask: true,
        })
           
         }else{
      const data = await post('/api/chen/dingzhi', {
        username: this.username,
        telnumber: this.telnumber,
        openid: this.userInfo.openId,
        nickname: this.userInfo.nickName,
        context: this.context
      })
      if (data) {
        wx.showToast({
          title: "提交成功",
          icon: "success",
          duration: 1500,
          mask: true,
          success: res => {
            _this.context = '',
              _this.telnumber = '',
              _this.username = ''
          }
        })
      }
    }
   },
   async get_liebiao(){
      const res = await get('/api/chen/splb',{
        owner : 'chenxing'
      })
      this.liebiaos = res.liebiao
   }
  },
  onLoad(){
    this.get_liebiao()
  }

};

